.buttonDiv{

    display: flex;
    justify-content: center;
    align-items: center;
    
}
.buttonDiv Button{
margin: 10px;
}
.mainDiv{

    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 100vh;

}



.animate {
    animation: scale 0.4s infinite linear;
  }

  .top {
    height: 80px;
    width: 100%;
    background: #f0514e;
    position: relative;
    box-shadow: 1px 6px 5px gainsboro;
  }  


  .top::before {
    content: "";
    position: absolute;
    height: 100%;
    width: 50%;
    right: 0;
    top: 0;
    background: #e43936;
  }

  .ring {
    position: absolute;
    width: 40px;
    height: 40px;
    background: #5952fd;
    border-radius: 50%;
    top: 20px;
  }
    .ring::before {
      content: "";
      position: absolute;
      top: -35px;
      height: 60px;
      width: 25%;
      left: 25%;
      background: #404041;
      border-radius: 20px 0 0 20px;
    }
    .ring::after {
      content: "";
      position: absolute;
      top: -35px;
      height: 60px;
      width: 25%;
      right: 25%;
      background: #1e1b1d;
      border-radius: 0 20px 20px 0;
    }

    .ring.left {
      left: 80px;
      background-color: white;
    }
    .ring.right {
      right: 80px;
      background-color: white;
    }

    .page {
        width: 100%;
        height: 320px;
        text-align: center;
        display: grid;
        grid-template-rows: 60px 260px;
        align-items: center;
        background: rgb(196, 239, 250);
    }
        .title {
          font-size: 2em;
          text-transform: uppercase;
          font-weight: bold;
          color: #3c3c3c;
        }
        .days {
          font-size: 25px;
          line-height: 25px;
          margin-bottom: 80px;
        }


